	module.exports = {"data":{"markdownRemark":{"html":"<p><strong><em>The Blue Nile</em></strong><br>\n<em>A Walk Across the Rooftops</em><br>\nNoir and glimmering torch songs elegantly wrung out of a high-end electronic studio. A best-of the 80s for me. Serendipitous acquisition: searched Reckless Records online catalog on a whim, saw it in stock, but the clerks still hadn't filed it on the racks. They were kind enough to pull it from the shelves behind the counter for me.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/blue-nile-a-walk-across-a4cb626a56d03e60249224c7889bdf12-8e2e9.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; margin-top:10px;; max-width: 300px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"blue-nile-a-walk-across\"\n        title=\"\"\n        src=\"/static/blue-nile-a-walk-across-a4cb626a56d03e60249224c7889bdf12-8e2e9.jpg\"\n        srcset=\"/static/blue-nile-a-walk-across-a4cb626a56d03e60249224c7889bdf12-dd96d.jpg 148w,\n/static/blue-nile-a-walk-across-a4cb626a56d03e60249224c7889bdf12-eaf39.jpg 295w,\n/static/blue-nile-a-walk-across-a4cb626a56d03e60249224c7889bdf12-8e2e9.jpg 300w\"\n        sizes=\"(max-width: 300px) 100vw, 300px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p><strong><em>Royer</em></strong><br>\n<em>Tough Questions</em><br>\nLoose and lo-fi house constructed from tender jazzy snippets. Well-plumbed low-end and tingling headroom palette. I'm a longtime Royer fan. This is an older release and I was surprised to find it still new on the racks while I was waiting for the Blue Nile LP to be located.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/royer-tough-qs-0898bc88e9b5467edd8f3182364942c0-8e2e9.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; margin-top:10px;; max-width: 300px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"royer-tough-qs\"\n        title=\"\"\n        src=\"/static/royer-tough-qs-0898bc88e9b5467edd8f3182364942c0-8e2e9.jpg\"\n        srcset=\"/static/royer-tough-qs-0898bc88e9b5467edd8f3182364942c0-dd96d.jpg 148w,\n/static/royer-tough-qs-0898bc88e9b5467edd8f3182364942c0-eaf39.jpg 295w,\n/static/royer-tough-qs-0898bc88e9b5467edd8f3182364942c0-8e2e9.jpg 300w\"\n        sizes=\"(max-width: 300px) 100vw, 300px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p><strong><em>Sam Prekop</em></strong><br>\n<em>Old Punch Card</em><br>\nA meandering assemblage of modular synthesizer explorations. Beeping, arpeggiation, artificial wind sounds kind of arranged with whimsical intuition of a children building castles out of colorful buckets and blocks. Also very sweet and meditative. When this album appeared without warning at my record store in Philadelphia years ago, I bought it on sight and had no idea it would be as abstract as it is. Thrill Jockey pressed a bunch of new editions a few months ago, but Wall of Sound in Seattle wasn't able to order them. But last night we saw The Sea and Cake and miraculously, some of the reissues were available at the merch table. This one, among a few hundred other of the recent repressings, has a hand-drawn cover done by Sam Prekop himself in pen.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/prekop-old-punch-card-6cdb4847785bd7da808721d3b429188d-8e2e9.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; margin-top:10px;; max-width: 300px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"prekop-old-punch-card\"\n        title=\"\"\n        src=\"/static/prekop-old-punch-card-6cdb4847785bd7da808721d3b429188d-8e2e9.jpg\"\n        srcset=\"/static/prekop-old-punch-card-6cdb4847785bd7da808721d3b429188d-dd96d.jpg 148w,\n/static/prekop-old-punch-card-6cdb4847785bd7da808721d3b429188d-eaf39.jpg 295w,\n/static/prekop-old-punch-card-6cdb4847785bd7da808721d3b429188d-8e2e9.jpg 300w\"\n        sizes=\"(max-width: 300px) 100vw, 300px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"date":"November 18, 2018","path":"/small-haul-1","title":"Blue Nile, Royer, and Sam Prekop"}}},"pathContext":{}}

